@import 'styles/_config.scss';

.root {
  display: flex;
  width: 100%;
  height: 100%;

  .container {
    padding: 0 25px;
    @include respond-below('sm') {
      padding: 0 20px;
    }
  }

  .wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    flex: 1 1 0%;
    overflow: hidden;
    @include respond-above('md') {
      margin-left: 300px;
      &.collapsed {
        margin-left: 85px;
      }
    }
  }

  .menuIcon {
    position: absolute;
    color: $primary;
    width: 60px;
    height: 60px;
    display: none;
    padding-top: 20px;
    margin-bottom: -10px;
    margin-left: -10px;
    cursor: pointer;
    z-index: 10;
  }

  .homeContent {
    margin: 0 auto;
    max-width: 80%;
    padding: 0 1rem;
  }

  @include respond-below('sm') {
    & {
      display: block;

      .menuIcon {
        display: block;
      }
    }
  }
}
