@import 'styles/_config.scss';

.wTab {
  background-color: palette(white);
  border: 2px solid $border-color;
  padding: 3px 16px;
  border-radius: $border-radius-big;
  margin-right: 7px;

  i,
  span,
  svg {
    color: $txt-dark;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
  }

  .wCoin {
    margin-left: 3px;
    display: inline-flex;
    justify-content: center;
  }

  // Disables :hover class below when Tab's disabled
  &:disabled {
    i,
    span,
    svg {
      color: grey !important;
    }
  }

  &:hover {
    i,
    span,
    svg {
      color: palette(blue);
    }
  }

  &.active {
    background-color: $blue;
    border-color: $blue;
    i,
    span,
    svg {
      color: palette(white);
    }
  }

  //
  // Icon positions
  //
  &.i-left {
    flex-flow: row;

    i,
    svg {
      margin-right: 10px;
    }
  }

  &.i-top {
    flex-direction: column;

    i,
    svg {
      margin-bottom: 10px;
    }
  }

  &.i-right {
    flex-flow: row-reverse;

    i,
    svg {
      margin-left: 10px;
    }
  }
}

.wTabs {
  display: inline-block;
  width: 100%;

  .wTabsRow {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 7px;

    div:first-child {
      flex-shrink: 0;
      max-width: 100%;
      overflow: scroll;
      display: flex;
      flex-wrap: nowrap;
      padding: 10px 0;

      button {
        flex-shrink: 0;
      }
    }
  }

  .wTabContent {
    display: flex;
    //overflow: scroll;
    flex: 1;
    height: 100%;
    width: 100%;
  }
}
