@import 'styles/_config.scss';

.root {
  margin: .5rem auto;
}
.AvatarCol {
  padding: 8px 0;
}
.smCol {
  border-left: 1px solid $border-color;
  padding-left: 20px;
  font-size: type-scale(0);
  color: $txt-dark;
  .label {
    color: $neutral;
    font-size: type-scale(-1);
  }
  a {
    color: $blue;
    &:hover, &:active {
      color: $blue-darker;
    }
  }
}