@import 'styles/_config.scss';

.root {
  z-index: 100;

  .avatar {
    cursor: pointer;
    right: 6px;
    z-index: 401;
  }

  .popover {
    

    .MenuItem {
      cursor: pointer;
      display: flex;
      font-size: 14px;
      align-items: center;
      justify-content: space-between;
      padding: 16px 25px;
      color:$txt-dark;
    }
    .danger {
      background-color: $danger;
      color: $white;
    }

    @include respond-above('sm') {
      & {
        right: 31px !important;
        top: 66px !important;
      }
      &.smaller {
        top: 56px !important;
      }
    }
  }
}
