@import 'styles/_config.scss';

.StatusPanel {
  padding-top: .5rem;
  padding-bottom: 1rem;
  display: flex;
  align-items: center;
  .StatusPanelLabel {
    padding-right: 5px;
    color: $neutral;
    font-size: type-scale(-1);
  }
}

.center {
  width: 100%;
  display: flex;
  justify-content: center;
}

.tableTitle {
  font-size: 14px;
  line-height: 18px;

  .email {
    color: $blue;
    font-size: 12px;
    line-height: 16px;
    margin: 2px 0 0 0;
  }
}
.cell {
  color: $blue;
  font-size: type-scale(0);
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;

  button:first-child {
    margin-right: 12px;
  }
}

.floatingButtons {
  position: fixed;
  bottom: -80px;
  right: 2rem;
  width: 172px;
  display: flex;
  z-index: 1030;
  background-color: $primary;
  padding: 4px;
  border-radius: $border-radius-big;
  transition: all .3s linear;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.1);

  &.activeApply {
    bottom: 2rem;
  }
}
.serverWidth {
  max-width: 300px;
}
.appsWidget {
  position: relative;
  margin: 5px 0;
  min-width: 285px;
  .avatar {
    color: $txt;
    border: none;
    border-right: 1px solid $border-color;
    border-radius: 8px 0 0 8px;
    box-shadow: unset;
    img {
      padding: 4px;
    }
  }
}

.statusPoint {
  position: absolute;
  top:10px;
  right:1rem;
}
// tabs
.appTab {
  position: relative;
  background-color: palette(white);
  border: 1px solid $border-color;
  border-radius: $border-radius-big;
  margin: 0.5rem 6px 1rem;
  display: flex;

  &.active {
    .appTabIcon {
      color: $white;
      background-color: $blue;
    }
  }

  .changesTracking {
    min-height: 40px;
    height: 100%;
    display: inline-flex;
    &:hover {
      i,
      span,
      svg {
        color: $white;
        background-color: $blue-darker;
      }
    }
  }

  .popover {
    top: 105%;
    left: 0;
    border-color: $border-color;
    // &::before {
    //   content: '';
    //   background-color: $white;
    //   width: 8px;
    //   height: 8px;
    //   transform: rotate(45deg);
    //   position: absolute;
    //   z-index: 501;
    //   top: -3px;
    //   left: 1rem;
    // }
    .popList {
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        padding: 6px 1rem;
        border-bottom: 1px solid $border-color-light;
        height: 44px;
        &.fitem {
          border-bottom: none;
        }
        &:last-child {
          border-bottom: none;
        }
        &:last-child {
          border-bottom: none;
        }
        .coin {
          margin-left: 3px;
        }
      }
    }
    .popSwitch {
      width: 100%;
      justify-content: space-between;
      font-size: type-scale(0);
      font-weight: 300;
    }
  }
  
  .appTabText {
    padding: 8px 16px;
  }
  
  .appTabIcon {
    height: 100%;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $neutral;
    background-color: $txt;
    border-radius: $border-radius-big 0 0 $border-radius-big;
    transition: all .3 linear;
  }

  .appLabel {
    width: 100%;
    padding: 2px 1rem;
    border-bottom: 1px solid $border-color-light;
    color: $neutral;
    font-size: type-scale(-1);
  }
  

  // &.active {
  //   background-color: $blue;

  //   i,
  //   span,
  //   svg {
  //     color: palette(white);
  //   }
  // }
}
.appTabContent {
  display: flex;
  flex: 1;
  height: 100%;
  width: 100%;
}
.ac {
  cursor: pointer;
}
.ac > [role="columnheader"]:first-child {
  margin: 0 !important;
}
.statusAlign {
  justify-content: start!important;
}
.stickyBtnGroup {
  position: sticky;
  top: 56px;
  background: $bg-color;
  z-index: 31;
}
.tableHeight {
  min-height: calc(100vh - 265px);
}