
@import 'styles/_config.scss';

.footer {
  font-size: type-scale(-1);
  &.center {
    text-align: center;
  }
}

.root {
  width: 100%;
  height: 100%;
  display: flex;
  align-self: center;
  justify-content: center;
  flex-direction: column;

  // .logoFlex {
  //   display: flex;
  //   justify-content: center;
  //   align-items: center;
  //   height: 80px;
  // }

  .card {
    position: relative;
    overflow: hidden;
    width: 50%;
    margin: 0 auto;
    padding: 2rem;
    a {
      text-decoration: underline;
      color: $blue;
    }
  }

  .logoFlex {
    position: relative;
    height: 80px;

  }
  .buttons {
    margin: 1rem 0;
  }

  .cardFlex {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    @include respond-below('md') {
      .loginContent {
        padding: 45px 45px 80px 45px;
      }
    }
  }

  
  
}
