// css variables for dynamic usage
:root {
  --primary: #0c1726;
  --secondary: #0099cf;
  --secondary-dark: #0078A5;
  --secondary-darker: #005878;
  --secondary-light: #78d0f7;
  --neutral: #90969c;
  --bgcolor: #f5f5f5;
  --bgcolor-rgb: 245,245,245;
  --txt: #606771;
  --txt-dark: #313a47;
  --white: #ffffff;
  --black: #000000;
  --border: #e4e9ed;
  --border-color: #e5e5e6;
  --border-color-light: #F2F2F2;
  --themefont: 'Montserrat';
}

html,
body,
#root {
  height: 100%;
  scroll-behavior: smooth;
  background-color: $bg-color;
  font-family: $font;
  overflow-x: hidden;
}

//
// ==================== G L O B A L S ====================
//
.gColumn {
  display: flex;
  flex-direction: column;
}
.gColumnCentered {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.gRow {
  display: flex;
  flex-direction: row;
}
.gRowCentered {
  align-items: center;
  display: flex;
  flex-direction: row;
}

.gFlexBetween {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

// Global class for required inputs - adds red `*` next to label
.gInputRequired::after {
  content: ' *';
  color: palette(red);
}

.gClickable {
  cursor: pointer !important;
}
