@import 'styles/_config.scss';

.wBackground {
  align-items: center;
  background: rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  display: flex;
  height: 100%;
  justify-content: center;
  left: 0;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  z-index: 34;
  
  .wDialog {
    background: white;
    display: flex;
    flex-direction: column;
    min-height: 300px;
    min-width: 400px;

    transform: translate(0, 0) scale(1);
    transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);

    .wHeader {
      padding: 1.5rem 1rem;
    }
    .wClose {
      cursor: pointer;
      position: absolute;
      right: 1rem;
      top: 1rem;
    }

    .wContent {
      display: flex;
      flex: 1;
      padding: 20px;
    }

    .wFooter {
      display: flex;
      justify-content: flex-end;
      padding: 10px 20px;
      & > button {
        margin-left: 5px;
      }
    }
  }
}
