// --------------------------
// Border radius
// --------------------------
// Please update documentation accordingly

$radius: (
  button: 3px,
  input: 2px,
  box: 2px,
);

@function radius($name) {
  @if map-has-key($radius, $name) {
    @return map-get($radius, $name);
  }

  @warn 'The key #{$name} is not in the map ’$radius’';
  @return null;
}

