@import 'styles/_config.scss';

.wHomePage {
  height: 100%;
}

.statusAlign {
  justify-content: start!important;
}

.scrollx {
  width: 100%;
  overflow-x: auto;
}

.logCell {
  height: 100%;
  justify-content: start!important;
  //white-space: normal;
}
.logsRow {
  //height: auto !important;
}
.listHeading {
  margin-left: 10px;
  font-size: 16px;
  color: $primary;
  font-weight: 500;
}
.logItem {
  border-bottom: 1px solid $border-color;
  font-size: type-scale(0);
  font-family: monospace;
  outline: none;
  padding: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
}

.fullWColl {
  display: flex;
  flex: 1;
  justify-content: space-between;
  font-size: type-scale(0);
  cursor: pointer;
  color: $neutral;
  i {
    color: $black;
  }
}
.headingRow {
  position: relative;
  margin-bottom: 10px;
  margin-top: 10px;
  h2 {
    margin: 0!important;
  }
  .downloadIcon {
    padding: 8px;
  }
}
.dropdown {
  justify-content: flex-end;
  > button {
    height: 34px!important;
    padding: 0 1rem;
  }
  nav {
    right: 0;
    left: unset !important;
  }
}
.noDataHeight {
  min-height: 300px;
}
.elips {
  overflow: hidden;
  white-space: nowrap;
  display: block!important;
  text-overflow: ellipsis;
}

.pointer {
  cursor: pointer;
}

.highlight {
  font-weight: 500;
  background-color: $warning;
  color: $white;
}