@import 'styles/_config.scss';

.root {
    opacity: 1;
    background-color: $bg-color;
    transition: all 0.5s ease-in-out;
    opacity: 1;
    height: 76px;
    width: 100%;
    //position: relative;
    z-index: 32;
    position: sticky;
    top: 0;

    &.headerSmaller {
        box-shadow: 0 2px 4px -1px rgba(0,0,0,0.25);
        height: 56px;
        .menuItem span{
            font-size: type-scale(0);
        }
    }

    .wrap {
        display: flex;
        flex: 1;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: space-between;
        padding-left: 25px;
        padding-right: 25px;
        .menuWrap {
            display: flex;
            height: 100%;
            align-items: center;
        }

        .menu {
            display: flex;
            height: 100%;
            align-items: center;

            .menuItemParent {
                transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
                margin: 0 25px;
                opacity: 1;
            
                .menuItem {
                    opacity: 1;
                    display: inline-flex;
                    position: relative;
                    text-decoration: none;
                    color: $primary;
                    font-weight: 400;
                    font-family: $themeFont;
            
                    span {
                        // position: absolute;
                        transition: all 0.5s ease-in-out;
                        z-index: 1;
                    }
            
                    .line {
                        transition: all 0.45s cubic-bezier(0.4, 0, 0.2, 1);
                        position: absolute;
                        width: 0;
                        margin-left: -5%;
                        height: 3px;
                        background-color: $blue;
                        bottom: -5px;
                    }
            
                }
            
                .activeLink {
                    color: $primary;
                }
            
                .menuItem.activeLink,
                .menuItem:hover,
                .menuItem:active {
                    .line {
                        width: 110%;
                        // z-index: -1;
                    }
                }
            
                .activee {
                    .line {
                        width: 116%;
                        // z-index: -1;
                    }
                }
            }
        }
        @include respond-below(sm) {
            .menu {
                display: none;
            }
        }
    }
}

.menuItemParent {
    @include respond-below(sm) {
        padding: 16px 20px;
        border-bottom: 1px solid $border;
    }
}

.hamburger {
    display: none;
    cursor: pointer;
    @include respond-below(sm) {
        display: block;
    }
}


.menuAction {
    display: flex;
    // height: 100%;
    max-width: 48px;
    align-items: center;
    outline: none;
    margin-left: 20px;
    margin-right: 5px;
    cursor: pointer;
}

.menuAction:focus,
.menuAction:active {
    opacity: 0.6;
}