@import 'styles/_config.scss';

.root {
  height: 24px;
  width: 100%;
  background-color: $bg-color;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}
.content {
  font-size: type-scale(-1);
  letter-spacing: 0;
  line-height: 12px;
  font-weight: 300;
  color: $txt-dark;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px;
}

.progress {
  height: 100%;
  border-radius: inherit;
  margin-right: 8px;
  transition: width 1s ease-in-out;
  overflow: hidden;

  &.danger {
    background-color: rgba($danger, .4);
  }
  &.primary {
    background-color: rgba($info, .4);
  }
  &.success {
    background-color: rgba($success, .4);
  }
  &.warning {
    background-color: rgba($warning, .4);
  }
}
