// Colors
$primary: var(--primary);
$blue: var(--secondary);
$blue-dark: var(--secondary-dark);
$blue-darker: var(--secondary-darker);
$blue-light: var(--secondary-light);
$neutral: var(--neutral);
$black: var(--black);
$white: var(--white);
$txt: var(--txt);
$txt-dark: var(--txt-dark);
$bg-color: var(--bgcolor);
$bg-color-rbg: var(--bgcolor-rgb);
$border: var(--border);
$border-color: var(--border-color);
$border-color-light: var(--border-color-light);

$info: #0099cf;
$danger: #ff2437;
$danger-light: #ff4a54;
$success: #6dd400;
$success-light: #32c83299;
$warning: #f7b500;
$warning-light: rgba(255, 229, 100, 0.6);


$border-radius: 4px;
$border-radius-big: 8px;

// Shadows
$shadow-light: 0 1px 4px 0 rgba(77, 73, 122, 0.15);

// animation
$animation-speed: 0.3s;