@import 'styles/_config.scss';

.wFullscreenBranded {
  align-items: center;
  cursor: wait;
  background: $bg-color;
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  justify-content: center;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  z-index: zIndex(max);

  img {
    max-width: 360px;
  }
}

