@import 'styles/_config.scss';

.wButtonDefault {
  cursor: pointer;
  transition: all $animation-speed ease-in-out;

  &:active,
  &:focus {
    outline: 0;
  }

  &:disabled,
  &:disabled:active {
    cursor: default;
    opacity: 0.6 !important;

    label {
      cursor: default;
    }
  }

  label {
    cursor: pointer;
    font-size: type-scale(0);
    font-weight: 500;
  }
}

.bLoading {
  cursor: wait !important;
}

.large {
  height: 56px;
  border-radius: $border-radius-big;

  label {
    font-size: 16px;
    line-height: 19px;
  }
}

.medium {
  height: 44px;
  border-radius: $border-radius-big;

  label {
    font-size: 16px;
    line-height: 19px;
  }
}

.small {
  height: 31px;
  border-radius: $border-radius-big;

  label {
    font-size: 13px;
    line-height: 16px;
  }
}

.danger {
  background-color: $danger;
  border: 2px solid $danger-light;
  padding: 0 20px;

  label,
  i {
    color: white;
  }
}

.blue {
  background-color: $blue;
  border: none;
  padding: 0 20px;

  label,
  i {
    color: white;
  }
}

.neutral {
  border: 2px solid $border-color;
  background-color: white;
  padding: 0 20px;

  label {
    color: $txt-dark;
  }

  i {
    color: $neutral !important;
  }
}

.neutralTrans {
  border: none;
  background-color: rgba($bg-color-rbg, 0.1);
  padding: 0 20px;

  label {
    color: $txt-dark;
  }

  i {
    color: $txt;
  }
}

.white {
  border: none;
  background-color: transparent;
  padding: 0 20px;
  label, i {
    color: $white;
  }
}

.transparent:hover,
.transparent:active,
.transparent:focus {
  &.danger {
    background-color: transparent;
    border: 2px solid $danger;

    label,
    i {
      color: $danger;
    }
  }

  &.blue {
    background-color: transparent;
    border: $blue;

    label,
    i {
      color: white;
    }
  }

  &.neutral {
    border: 2px solid white;
    background-color: $border-color;
    
    label, i {
      color: $border-color !important;
    }
  }
}

.shadow:hover,
.shadow:focus,
.shadow:active {
  &.danger {
    box-shadow: 4px 10px 20px 0 rgba($danger, 0.5);
  }

  &.blue {
    box-shadow: 4px 10px 20px 0 rgba($info, 0.5);
  }

  &.neutral {
    box-shadow: 4px 10px 20px 0 rgba($border-color, 0.5);
  }
}

.icon {
  border-radius: $border-radius-big;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;

  &.small {
    width: 28px;
    height: 28px;
  }

  &.medium {
    width: 34px;
    height: 34px;
  }

  &.large {
    width: 48px;
    height: 48px;
  }
}
