@import 'styles/_config.scss';

// .widgets {
//   width: 100%;
// }
.table {
  width: 100%;
  overflow-x: auto;
}
.serverCard, .spCard {
  width: 100%;
  // flex:1 1 auto;
  // max-width: 50%;
  // margin: 0.5rem;
}
.ac > [role="columnheader"]:first-child {
  margin: 0 !important;
}

.header {
  margin-bottom: 20px;
  position: relative;
}
.chipsLine {
  min-height: 20px;
}
.cardHeading {
  color: $txt-dark;
  font-size: type-scale(1) !important;
  margin: 0 !important;
}

.neuralSub {
  display: block;
  font-size: type-scale(-1);
  color: $neutral;
  margin-bottom: .5rem;
}

.input {
  max-width: 300px;
}

.cakeLabelTop {
  transform: translateY(-5px);
}
.cakeLabelBottom {
  transform: translateY(5px);
}
.spTable {
  width: 100%;
  max-height: 430px;
  overflow-y: auto;
  table {
    width: 100%;
  }
  thead {
    font-weight: 500;
  }
  tr {
    border-bottom: 1px solid $border-color-light;
    &:last-child {
      border-bottom: none;
    }
    >td {
      padding: 6px 0;
      &:last-child {
        text-align: right;
      }
      small {
        display: block;
        color: $neutral
      }
    }
  }
}

.userListTable {
  width: 100%;
  max-height: 430px;
  overflow-y: auto;
  table {
    width: 100%;
  }
  thead {
    font-weight: 500;
  }
  tr {
    border-bottom: 1px solid $border-color-light;
    &:last-child {
      border-bottom: none;
    }
    >td {
      padding: 6px 0;
    }
  }
}

.TabBtn {
  padding: 8px;
  border-radius: $border-radius-big;
  i {
    margin-right: 0!important;
  }
}
.link {
  cursor: pointer;
  color: $blue;
}
.tabsHeading {
  h1 {
    margin-bottom: 5px!important;
  }
}