@import 'styles/_config.scss';

.root {
  min-width: 100px;
  height: 100px;
  display: flex;
  flex-direction: column;
  padding: 12px 12px;
  border-radius: $border-radius-big;
  background-color: $border-color;
  color: $neutral;
  margin-bottom: 10px;
  &.darkerBlue {
    background-color: $blue-darker;
    color: $white;
  }
  &.darkBlue {
    background-color: $blue-dark;
    color: $white;
  }
  &.blue {
    background-color: $blue;
    color: $white;
  }
  &.lightBlue {
    background-color: $blue-light;
    color: $white;
  }
  &.success {
    background-color: $success;
    color: $white;
  }
  &.warning {
    background-color: $warning;
    color: $white;
  }
  &.danger {
    background-color: $danger;
    color: $white;
  }
  &.neutral {
    background-color: $border-color;
    color: $neutral;
  }

  >h3 {
    font-size: type-scale(2);
    line-height: 1.25;
    white-space: nowrap;
    user-select: none;
  }
  >small {
    font-size: type-scale(-1);
    line-height: normal;
    height: 30px;
    display: flex;
    align-items: flex-end;
    word-break: break-word;
    user-select: none;
  }
  &.cursor {
    cursor: pointer;
  }
}