@import 'styles/_config.scss';

.wChip {
  align-items: center;
  border-radius: 16px;
  display: inline-flex;
  justify-content: center;
  padding: 4px 15px;
  white-space: nowrap;
  transition: all .3s linear;

  // Label
  span {
    font-size: 12px;
    font-weight: 500;
    line-height: 14px;
  }

  div {
    margin-left: 10px;
  }
}

.bDisabled {
  opacity: 0.6;
}
// size
.small {
  font-size: 12px;
  padding: 1px 8px;
}

.medium {
  font-size: 12px;
  padding: 4px 15px;
}

.large {
  font-size: 14px;
  padding: 4px 15px;
}

.fill {
  &.white {
    background-color: #ffffff;
    color: $txt;
  }
  &.neutral {
    background-color: $border-color;
    color: $neutral;
  }
  &.blue {
    background-color: #d9f0f8;
    color: $blue;
  }
  &.primary {
    background-color: $primary;
    color: $white;
  }
  &.success {
    background-color: $success;
    color: $white;
  }
  &.danger {
    background-color: $danger;
    color: $white;
  }
  &.warning {
    background-color: $warning;
    color: $white;
  }
}

.outline {
  border: 1px solid palette(blue);
}
