@import 'styles/_config.scss';

// Dimensions
$small: 24px;
$medium: 40px;
$large: 56px;
$huge: 100px;

.wAvatar {
  align-items: center;
  background-color: white;
  border: 1px solid white;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  overflow: hidden;
  text-transform: uppercase;
  transition: all $animation-speed ease-in-out;

  .wImg {
    height: 100%;
    width: 100%;
  }

  // Label
  span {
    color: $black;
    font-size: 1.25rem;
  }
}

.wAvatarGroup {
  display: flex;

  .wAvatarStack {
    margin-right: -10px;
  }
}

//
//  Shape variants
//
.circle {
  border-radius: 50%;
}
.round {
  border-radius: 15%;
}
.square {
  border-radius: $border-radius-big;
}

.expandableCircle {
  border-radius: 25px;
}

//
//  Size variants
//
.small {
  height: $small;
  width: $small;
}
.medium {
  height: $medium;
  width: $medium;
}
.large {
  height: $large;
  width: $large;
}

.huge {
  height: $huge;
  width: $huge;
}

.expanded {
  width: 240px;
  height: $medium;
  border-radius: 25px;
  text-transform: none;
  span {
    font-size: 1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  @include respond-below('md') {
    width: $medium;
    text-transform: uppercase;
  }
}