@import 'styles/_config.scss';

.root {
  .headerRow {
    position: relative;
    margin-bottom: 1rem;
    margin-top: 10px;
  }
}

.refresh {
  display: inline-flex;
  justify-self: end;
  align-items: center;
  i {
    margin-right: 5px;
  }
}

.Cell {
  font-size: type-scale(0);
}
.recordCell {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: $txt-dark;
  color: $white;
  padding-left: 1rem;
  padding-right: 1rem;
  i {
    cursor: pointer;
  }
}

.appCell {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 1rem;
  padding-right: 1rem;
  .label {
    color: $neutral;
    font-size: type-scale(-1);
  }
  .appCellLinks {
    display: flex;
    align-items: center;
    margin: 0;
    &.noLink {
      span {
        color: $black;
        cursor: default;
      }
    }
    span,i {
      cursor: pointer;
    }
    i {
      margin-left: 3px;
    }
    span {
      color: $blue;
      &:hover {
        color: $blue-darker;
      }
    }
  }
}

.defaultCell {
  display: inline-flex;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 0;
  > div {
    margin-right: 1rem;
    overflow: hidden;
  }
}

.userCell {
  display: inline-flex;
  margin: 0!important;
  svg {
    margin: 0 3px;
    fill: $txt;
  }
}

.warningsCell {
  display: flex;
  color: $border-color;
  font-weight: 300;
  margin: 0;
  i {
    margin-right: 4px;
  }
  &.has {
    color: $warning;
    cursor: pointer;
  }
}

.actionCell {
  height: 100%;
  margin: 0 !important;
}
.ac {
  margin: 0 !important;
}
.ac > [role="columnheader"] {
  margin: 0 !important;
}
.ac > [role="columnheader"]:first-child {
  margin: 0 !important;
}
.ac > [role="columnheader"]:nth-child(2) {
  padding: 0 1rem;
}

.actionCellDropdown {
  width: 100%;
  position: inherit !important;
  cursor: pointer;
  border-right: 1px solid $border-color;
  > button {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.MetricsCellChild {
  width: 100%;
  padding-right: 1rem;
  h4 {
    font-size: type-scale(-1);
    color: $primary;
    font-weight: 500;
  }
}

.tableHeight {
  min-height: calc(100vh - 282px);
}

.btnstab {
  margin: 0.5rem 0 0;
}
.rightSpace {
  margin-right: 12px;
}