@import 'styles/_config.scss';

.root {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  > button {
    height: 100%;
    display: flex;
    align-items: center;
  }

  .menu {
    background-color: $txt-dark;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);
    border-radius: 8px;
    position: absolute;
    min-width: 180px;
    top: 100%;
    z-index: 101;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s;
    display: flex;
    flex-direction: column;

    &.right {
      right: 0;
    }

    &.left {
      left: 0;
    }

    .button {
      position: relative;
      display: flex;
      align-items: center;
      white-space: nowrap;
      color: $white;
      border-bottom: 1px solid $txt;
      transition: all .3 linear;
      &.sub {
        background-color: $primary;
        padding-left: 2rem;
      }

      &.danger {
        background-color: $danger-light;
        color: $white;
        i {
          color: $white;
        }
      }

      &.disabled, &.disabled:hover{
        background-color: #454d59;
        color: $txt;
        i {
          color: $txt;
        }
      }

      &:first-of-type {
        border-radius: 8px 8px 0 0;
      }

      &:last-of-type {
        border: none;
        border-radius: 0 0 8px 8px;
      }

      &:hover {
        color: $txt;
        i {
          color: $txt;
        }
      }

      i {
        color: $txt;
        margin-right: 10px;
        transition: all .3 linear;
      }

      .btnText {
        height: 100%;
        padding: 1rem;
        display: flex;
        &.fullW {
          width: 100%;
        }
      }

      .expandIcon {
        height: 100%;
        position: relative;
        right: 0;
        display: flex;
        align-items: center;
        margin: 0;
      }
    }
  }
  .menuActive {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
