@import 'styles/_config.scss';

.root {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background-color: $primary;
  width: 300px;
  flex-shrink: 0;
  min-height: 100vh;
  display: flex;
  position: fixed;
  z-index: 33;
  top: 0px;
  flex-direction: column;

  .logo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 38px;
    margin: 30px 25px 16px;

    .webswingLogo {
      z-index: 1;
    }
    img {
      height: 38px;
    }
  }
  .toolRow {
    padding: 10px 25px;
    flex-wrap: wrap;
  }
  .appsFilter {
    position: relative;
    flex: 1;
    .input {
      width: 100%;
      color: #7E858E;
      background-color: rgba($bg-color-rbg, 0.1);
      border: 1px solid transparent;
      border-radius: 8px;
      height: 34px;
      padding: 6px 12px;
      outline: none;
      &:focus {
        color: $white;
        border-color: $white;
      }
    }
    .appsFilterIcon {
      position: absolute;
      display: flex;
      color: #7E858E;
      height: 34px;
      align-items: center;
      left: 24px;
    }
  }

  .sideMenuArrow {
    width: 32px;
    height: 32px;
    background: $blue;
    position: absolute;
    bottom: 20%;
    right: 0;
    z-index: 101;
    bottom: 15%;
    right: -16px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 50%;
    .arrowIcon {
      color:$white;
      transform: rotate(0);
    }
  }

  .createApp {
    position: absolute;
    bottom: 76px;
    left: 0;
    right: 0;
    background-color: $primary;
    a {
      padding: 10px 15px;
    }
  }

  .downloadFlex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .absIcon {
    position: absolute;
    right: 25px;
  }

  .scrollable {
    position: relative;
    display: block;
    overflow-y: auto;
    height: calc(100vh - 275px);
    width: 100%;

  }
  .menuFlex {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    //height: calc(100% - 180px);

    flex: 1;
    .menuItems {
      padding-left: 0;

      .menuItemIcon {
        height: 34px;
        width: 34px;
        color: $txt;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 23px;
      }
    }
  }

  .developed {
    font-size: 12px;
    line-height: 16px;
    margin-left: 34px;
    color: $neutral;

    a {
      color: white;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .menuCloseIcon {
    display: none;
    cursor: pointer;
  }

  .inputCreate {
    border: none;
    background-color: $bg-color!important;
    max-width: 130px;
    font-size: type-scale(0);
    line-height: 21px;
    padding: 0 12px;
    color: $txt-dark
  }
  .inputSmall {
    color: $txt;
    font-size: 75%;
  }

  .menuItem {
    display: flex;
    align-items: center;
    color: $txt-dark;
    position: relative;
    padding: 10px 25px;
    cursor: pointer;

    img {
      height: 34px;
      background: white;
      border-radius: 8px;
    }

    .text {
      display: flex;
      flex-direction: column;
      margin-left: 12px;
      color: $border-color;
      font-family: $themeFont;
      font-size: type-scale(0);
      width: 100%;
      small {
        color:$neutral; 
      }
    }

    &:hover,
    &.activeLink {
      .text {
        font-weight: 500;
        color: white;
      }

      .icon {
        color: $txt;
      }
    }
    
    &.activeLink:before {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      height: 100%;
      width: 98%;
      border-left: 4px solid $blue;
      
      border-radius: $border-radius 0 0 $border-radius;
      background-color: $blue;
      background-color: rgba($bg-color-rbg, 0.1);
    }
    &.activeLink {
      position: relative;
      z-index: 2;
      background-color: $primary;
    }
  }
  .addApp::before{
    background-color: $bg-color!important;
    z-index: -1;
  }

  &.collapsed {
    width: 85px;
    .menuItems {
      .text {
        display: none;
      }
    }
    .menuItem, .logo {
      justify-content: center;
    }
    .arrowIcon {
      transform: rotate(180deg)!important;
    }
    .developed {
      display: none;
    }
  }
  
  // @include respond-below('md') {
  // }
  // @include respond-below('sm') {
  // }
  @include respond-below('xs') {
    & {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      max-width: 100%;
      z-index: 200;
      margin-left: -300px;

      &.show {
        margin-left: 0;
      }

      .menuCloseIcon {
        display: block;
        //margin-top: -20px;
      }
    }
  }
}
.wfull {
  width: 100%;
}
.divider {
  height: 1px;
  width: 100%;
  background-color: rgba($bg-color-rbg, 0.1);
  margin: 4px 0;
}
.footer {
  font-size: type-scale(-1);
  margin-bottom: 20px;
  &.center {
    text-align: center;
  }
}