@import 'styles/_config.scss';

.wList {
  display: flex;
  flex: 1;
  display: flex;
  flex: 1 1 auto;
  min-width: calc(100vw - 900px);
  min-height: calc(100vh - 200px);
  max-width: 100vw;

  @include respond-below('sm') {
    max-width: initial;
  }
  .wListVirtualized {
    background-color: white;
    box-shadow: $shadow-light;
    border-radius: $border-radius-big;
    border: 1px solid $border-color;
    outline: none;
    width: 100%;
  }

}

.wListRegular {
  display: flex;
  flex: 1;
  flex-direction: column;
}
.noWrap {
  white-space: nowrap;
}

.fab {
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  z-index: 2;
  background-color: rgba(	0, 153, 207, 0.5);
  &:hover {
    background-color: $blue;
  }
}