@import 'styles/_config.scss';

.root {
  .wRow {
    position: relative;
    height: 60px;
    @include respond-below('md') {
      height: auto;
      flex-direction: column;
    }
  }
  .Acell {
    max-width: 35px;
    @include respond-below('md') {
      max-width: 100%;
      border-right: none;
      border-bottom:  1px solid $border-color;
      padding: 0.5rem 1rem;
    }
  }
  .noBorder {
    border-left: none !important;
  }
  .smCol {
    border-left: 1px solid $border-color;
    padding: 0 1rem;
    font-size: type-scale(0);
    color: $txt-dark;
    justify-content: center;
    height: 100%;
    .label {
      color: $neutral;
      font-size: type-scale(-1);
    }
    &.appCol {
      padding: 0;
      border-left: unset;
      flex: 1;
      min-width: 148px;
      @include respond-below('md') {
        padding: 0.5rem 0;
      }
    }
    @include respond-below('md') {
      height: auto;
      border-left: none;
      border-bottom:  1px solid $border-color;
      padding: 0.5rem 1rem;
      &:last-of-type {
        border-bottom: none;
      }

    }
  }
  .minW {
    min-width: 149px;
  }
}

.wh {
  width: 100%;
  height: 100vh;
}
.lineChart {
  padding: 1rem;
}

.cardContent {
  max-width: 100%;
  overflow: auto;
}
.cardSpacer {
  padding: 1.5rem 2rem;
}
.iframeWrap {
  width: 100%;
  overflow-x:auto;
}

.btnGroupRow {
  margin-bottom: 1rem;
}

.toolBar {
  margin: 1rem auto;
}
.backButton {
  display: flex;
  color:$txt-dark;
  font-size: type-scale(-1);
  margin: auto;
  padding: 9px 20px;
}

.Warnings {
  width: 100%;
  .warningsListItem {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 16px;
    color:$txt-dark;
    font-size: type-scale(0);
    a {
      color:$blue;
      cursor: pointer;
      &:hover, &:active {
        color: $blue-darker;
      }
    }
  }
  .warningLine {
    border-bottom: 1px solid $border-color;
    &:last-of-type {
      border-bottom: none;
    }
  }
}
.hiddenMirror {
  display: none;
}
.activeContent {
  display: block!important;
}
.mirrorView {
  display: flex;
  flex-direction: column;
  margin: 1.5rem auto;
  transition: all $animation-speed ease-in-out;
  .mirrorRow {
    margin-bottom: 1rem;
  }
  .minMax {
    display: flex;
    justify-content: center;
    cursor: pointer;
  }
  &.hiddenMirror {
    display: none;
  }

  &.fullScreen {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    bottom: 0;
    height: 100%;
    background: white;
    z-index: 34;
    padding: 1rem;
    margin: 0;
    .cardContent {
      height: calc(100% - 60px);
    }
  }
}
.tableHeight {
  > div {
    min-height: calc(100vh - 335px) !important;
  }
}
.m7 {
  margin-right: 7px;
}