@import 'styles/_config.scss';

.root {
  display: block;
  width: 1em;
  line-height: 1em;
  overflow: hidden;

  // color
  &.primary {
    color: $primary;
  }
  &.white {
    color: $white;
  }
  &.light {
    color: $light;
  }
  &.txt {
    color: $txt;
  }
  &.blue {
    color: $blue;
  }
  &.neutral {
    color: $neutral;
  }
  &.success {
    color: $success;
  }
  &.warning {
    color: $warning;
  }
  &.danger {
    color: $danger;
  }

  // sizes
  &.mini {
    font-size: 16px;
  }
  &.small {
    font-size: 20px;
  }
  &.medium {
    font-size: 24px;
  }
  &.large {
    font-size: 28px;
  }
  &.extraLarge {
    font-size: 38px;
  }
}
